@import "sass-rem";

.headerBottom {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: rem(535px);
        background: linear-gradient(90deg, #262626 0%, #1A1A1A 56.77%, #000000 100%);
    }
}

.content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(14px 25px);
    z-index: 2;
}

.searchLabel {
    position: relative;
    width: rem(280px);
}

.searchIcon {
    position: absolute;
    top: 50%;
    left: rem(3px);
    width: rem(16px);
    height: rem(16px);
    background: url("../../assets/images/svg/header-search.svg") no-repeat 50% 50% /contain;
    transform: translateY(-50%);
}

.searchIconPath {
    fill: #898989;
    transition: .3s;
}

.search {
    width: 100%;
    min-height: rem(40px);
    padding: rem(10px 10px 10px 35px);
    font-size: rem(16px);
    color: #fff;
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;

    &::placeholder {
        color: #898989;
        opacity: 1;
    }

    &:focus {
        & + .searchIcon .searchIconPath {
            fill: #fff;
        }
    }
}

.list {
    display: flex;
    gap: rem(5px);
    align-items: center;
}

.link {
    display: inline-block;
    position: relative;
    font-size: rem(14px);
    line-height: rem(16px);        
    color: #696969;
    padding: rem(10px);

    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: rem(-5px);
        right: rem(-5px);
        background: rgba(255, 255, 255, 0);
        transition: .3s;
        border-radius: rem(20px);
        z-index: -1;
    }

    &:hover,
    &.linkActive {
        color: #fff;
        &:before {
            background: #1f1f1f;
        }
    }

    &.linkCustom {
        text-transform: uppercase;
        color: #B7181B;
        padding: rem(5px 13px);
        border: 1px solid #B7181B;
        border-radius: rem(30px);
        font-style: italic;
        font-weight: 700;
        &:hover {
            color: #B7181B;
        }
        &:before {
            display: none;
        }
    }
}

.info {
    display: flex;
    align-items: center;
    gap: rem(15px);
}

.time {
    font-size: rem(14px);
    line-height: rem(16px);
    color: #696969;
}

.apps {
    display: flex;
    align-items: center;
    gap: rem(15px);
}

.linkApp {
    display: inline-block;
    border-radius: 50%;
    font-size: 0;
    width: rem(40px);
    height: rem(40px);
    position: relative;

    &:before {
        content: "";
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 50% 50%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &.linkAppIphone {
        background: #fff;
        
        &:before {
            width: rem(18px);
            height: rem(21px);
            background-image: url("../../assets/images/svg/header-app-iphone.svg");
        }
    }

    &.linkAppAndroid {
        background: #7EA152;

        &:before {
            width: rem(22px);
            height: rem(16px);
            background-image: url("../../assets/images/svg/header-app-android.svg");
        }
    }
}

.selectLanguage {
    display: flex;
    align-items: center;
    border: 2px solid #262626;
    border-radius: 30px;
    height: rem(40px);
    padding: rem(10px 40px 9px 15px);
    font-size: rem(18px);
    line-height: rem(21px);
    color: #fff;
    position: relative;
    cursor: pointer;
    transition: .3s;

    &:before {
        content: "";
        position: absolute;
        top: 50%;
        right: rem(15px);
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(18px);
        height: rem(18px);
    }


    &.ru {
        &:before {
            background-image: url("../../assets/images/svg/header-flag-ru.svg");
        }
    }

    &:hover {
        border-color: #fff;
    }   
}